import { useMemo } from 'react';
var COM_TLDS = ['tr', 'am', 'ge'];
var CO_TLDS = ['il'];
export function useTLD(tld) {
    var result = useMemo(function () {
        if (COM_TLDS.includes(tld))
            return "com." + tld;
        if (CO_TLDS.includes(tld))
            return "co." + tld;
        return tld;
    }, [tld]);
    return result;
}
